/*Обнуление*/
*{
  padding: 0;
  margin: 0;
  border: 0;
}

*,*:before,*:after{
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,:active{
  outline: none;
}
a:focus,a:active{
  outline: none;
}
nav,footer,header,aside{
  display: block;
}
html,body{
  height:100%;
  width:100%;
  font-size:100%;
  line-height:1;
  font-size:14px;
}
input,button,textarea{
  font-family:inherit;
}
button{
  cursor: pointer;
}
a,a:visited{
  text-decoration: none;
}
a:hover{
  text-decoration: none;
}
ul li{
  list-style: none;
}
h1,h2,h3,h4,h5,h6{
  font-size:inherit;
  font-weight: inherit;
}
